<template>
  <div class="moren">
      <div class="hot">
         <h2 class="hot-title">
           热门MV
         </h2>
        <div class="container">
            <ul class="mv-list">
                <li class="mv-list-item" v-for="(item, index) in list" :key="index">
                    <div class="mv-list-item-t">
                        <div class="owner-info">
                          <img class="head" :src="item.singers[0].picurl" >
                          <p class="name">{{item.singers[0].name}}</p>
                        </div>
                        <div class="title">{{item.title}}</div>
                    </div>
                    <div class="mv-list-item-c">
                      <video  :poster="item.picurl"
                         controls  class="media" :src="`${mvUrlAPI}?id=${item.vid}&quality=1080`"></video>
                    </div>
                </li>
            </ul>
        </div>
    </div>
  </div>
</template>

<script>
import {hotMvAPI,mvUrlAPI} from '@/api'
import { Indicator } from 'mint-ui'
export default {
  name: 'More',
  data() {
    return {
      list:[],
      mvUrlAPI
    }
  },
  methods: {
      getMvList(){
          Indicator.open('加载中...');
        this.axios.get(`${hotMvAPI}?pageSize=10`)
          .then(res =>{
            this.list = res.data.data.list
             Indicator.close();
            //console.log(res)
          })
      }
  },
  mounted() {
    this.getMvList()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.hot{
  .hot-title{
    height: 100px;
    line-height: 100px;
    color: #000;
    font-size:36px;
    position: relative;
    text-align: center;
  }
}
 .container{
   .mv-list{
     .mv-list-item{
       margin: 10px 23px;
       border-bottom: 1px solid #eee;
       .mv-list-item-t{
         .owner-info{
           height: 120px;
           line-height: 120px;
           .head{
             height: 60px;
             width: 60px;
            border-radius: 50%;
             float: left;
             margin-top: 30px;
             margin-right: 23px;
           }
          .name{
            font-size: $primary-font-size;
            color: #000;
          }
         }
         .title{
           height: 50px;
           line-height: 50px;
            font-size: $primary-font-size;
            color: #000;
         }
       }
       .mv-list-item-c{
         video{
          height: 380px;
           width: 100%;
         }
        
       }

     }
   }

 } 

</style>
